@import "../../utils/colors.scss";

.popup__wrap, .active{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $popup;
    display: none;

    .popup{
        padding: 20px;
        background-color: white;
        width: 400px;

        h2{
            margin-bottom: 20px;
        }
        input{
            width: 100%;
            margin-bottom: 20px;
            padding: 5px 10px;
        }
        .btn__block{
            display: flex;
            justify-content: space-between;
            width: 200px;
            float: right;
        }
    }
}
.active{
    display: flex;
}